<template>
  <div class="select">
    <fx-card title="基础用法">
      <fx-select v-model="baseSelect" placeholder="请选择">
        <fx-option v-for="item in options" :key="item.value"
                   :label="item.label" :value="item.value">
        </fx-option>
      </fx-select>
      <template #hide>
        <pre v-highlight="base"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="禁用部分选项">
      <fx-select v-model="disabledSelect" placeholder="请选择">
        <fx-option v-for="item in options" :key="item.value" :disabled="item.disabled"
                   :label="item.label" :value="item.value">
        </fx-option>
      </fx-select>
      <template #hide>
        <pre v-highlight="disabled"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="禁用状态">
      <fx-select v-model="disabledSelects" placeholder="请选择" disabled>
        <fx-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"/>
      </fx-select>
      <template #hide>
        <pre v-highlight="disable"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="可清空">
      <fx-select v-model="clearSelect" placeholder="请选择" clear>
        <fx-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"/>
      </fx-select>
      <template #hide>
        <pre v-highlight="clear"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="多选">
      <fx-select class="mr20" v-model="multipleSelect" placeholder="请选择" multiple>
        <fx-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"/>
      </fx-select>
      <fx-select v-model="collapseSelect" placeholder="请选择" multiple collapse>
        <fx-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"/>
      </fx-select>
      <template #hide>
        <pre v-highlight="multiple"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="自定义模板">
      <fx-select v-model="ownSelect" placeholder="请选择">
        <fx-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"
                   style="display:flex;justify-content:space-between;align-items: center">
          <span>{{ item.label }}</span>
          <fx-button size="mini" type="primary">按钮</fx-button>
        </fx-option>
      </fx-select>
      <template #hide>
        <pre v-highlight="own"><code></code></pre>
      </template>
    </fx-card>
  </div>
</template>

