    const collapseSelect = ref<string[]>(['娜美', '妮可·罗宾'])
    const ownSelect = ref<string>('蒙奇·D·路飞')

    const base = `<fx-select v-model="baseSelect" placeholder="请选择">
  <fx-option v-for="item in options" :key="item.value"
    :label="item.label" :value="item.value">
  </fx-option>
</fx-select>
const options = reactive([
  { value: '蒙奇·D·路飞', role: '船长', label: '蒙奇·D·路飞' },
  { value: '罗罗诺亚·索隆', role: '剑士', label: '罗罗诺亚·索隆' },
  { value: '文斯莫克·山治', role: '厨师', label: '文斯莫克·山治' },
  { value: '托尼托尼·乔巴', role: '医生', label: '托尼托尼·乔巴', disabled: true },
  { value: '娜美', role: '航海士', label: '娜美', disabled: true },
  { value: '乌索普', role: '狙击手', label: '乌索普' },
  { value: '妮可·罗宾', role: '考古学家', label: '妮可·罗宾', disabled: true },
  { value: '弗兰奇', role: '船匠', label: '弗兰奇' },
  { value: '布鲁克', role: '音乐家', label: '布鲁克' },
  { value: '甚平', role: '舵手', label: '甚平' }
])`

    const disabled = `<fx-select v-model="baseSelect" placeholder="请选择">
  <fx-option v-for="item in options" :key="item.value"
    :label="item.label" :value="item.value">
  </fx-option>
</fx-select>
const options = reactive([
  { value: '蒙奇·D·路飞', role: '船长', label: '蒙奇·D·路飞' },
  { value: '罗罗诺亚·索隆', role: '剑士', label: '罗罗诺亚·索隆' },
  { value: '文斯莫克·山治', role: '厨师', label: '文斯莫克·山治' },
  { value: '托尼托尼·乔巴', role: '医生', label: '托尼托尼·乔巴', disabled: true },
  { value: '娜美', role: '航海士', label: '娜美', disabled: true },
  { value: '乌索普', role: '狙击手', label: '乌索普' },
  { value: '妮可·罗宾', role: '考古学家', label: '妮可·罗宾', disabled: true },
  { value: '弗兰奇', role: '船匠', label: '弗兰奇' },
  { value: '布鲁克', role: '音乐家', label: '布鲁克' },
  { value: '甚平', role: '舵手', label: '甚平' }
])`

    const disable = `<fx-select v-model="disabledSelects" placeholder="请选择" disabled>
  <fx-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"/>
</fx-select>`

    const clear = `<fx-select v-model="clearSelect" placeholder="请选择" clear>
  <fx-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"/>
</fx-select>`

    const multiple = `<fx-select class="mr20" v-model="multipleSelect" placeholder="请选择" multiple>
  <fx-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"/>
</fx-select>
<fx-select v-model="collapseSelect" placeholder="请选择" multiple collapse>
  <fx-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"/>
</fx-select>`
    const own = `<fx-select v-model="ownSelect" placeholder="请选择">
  <fx-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"
             style="display:flex;justify-content:space-between;align-items: center">
    <span>{{ item.label }}</span>
    <fx-button size="mini" type="primary">按钮</fx-button>
  </fx-option>
</fx-select>`

    return {
      options,
      baseSelect,
      disabledSelect,
      disabledSelects,
      clearSelect,
      multipleSelect,
      collapseSelect,
      ownSelect,
      base,
      disabled,
      disable,
      clear,
      multiple,
      own
    }
  }
})
</script>

